.Button {
  align-items: center;
  background: #333;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  font-size: 45px;
  height: 110px;
  justify-content: center;
  width: 110px;
  transition: -webkit-filter 0.3s ease-in;
  transition: filter 0.3s ease-in;
  transition: filter 0.3s ease-in, -webkit-filter 0.3s ease-in;
}

.Button:active,
.Button:focus {
  -webkit-filter: brightness(250%);
          filter: brightness(250%);
}

.Button.zero {
  width: 250px;
  grid-column: 1 / span 2;
  border-radius: 57.5px;
  position: relative;
  justify-content: flex-start;
  padding-left: 43px;
}

.Button.function {
  color: black;
  background: #a5a5a5;
}

.Button.operator {
  background: #ff9b04;
}

.App {
	background: black;
	border-radius: 50px;
	color: white;
	height: 1218px;
	padding: 20px;
	position: relative;
	width: 563px;
	/* center the div */
	margin: 0 auto;
	margin-top: 10%;
}

.top {
	display: flex;
	height: 250px;
	/* justify-content: space-between; */
	padding: 0 5px;
}

.time {
	margin-left: 128px;
}
.display {
	font-size: 130px;
	font-weight: 300;
	margin-bottom: 20px;
	text-align: right;
}

.buttons {
	display: grid;
	grid-gap: 20px;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(5, 1fr);
}

.bottom {
	position: absolute;
	width: 200px;
	height: 5px;
	background: white;
	bottom: 10px;
	left: 50%;
	border-radius: 4px;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

img {
	visibility: hidden;
}

* {
  box-sizing: border-box;
}

html {
  position: absolute;
  background: url("https://images.unsplash.com/photo-1601662528567-526cd06f6582?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1258&q=80")
    center center/cover;
  width: 100%;
  height: 100vh;
}

body {
  margin: 0;
  font-family: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

